import { useEffect, useRef, useState } from "react";
import useWindowSize from "../GAPP/Infraction/hook/useWindowSize";
import { IMAGE_WEBP_QUALITY } from "../../Util/Util";

// --- COMPONENTE DE RECORTE DE IMAGEM (INTEGRADO) ---
interface ImageCropperProps {
  imageSrc: string;
  onCrop: (croppedImage: string) => void;
  onCancel: () => void;
}

/**
 * @interface ImageCropperProps
 * @property {string} imageSrc - A URL da imagem que ser exibida para recorte (geralmente um Base64).
 * @property {(croppedImage: string) => void} onCrop - Funo de callback que  chamada quando a imagem  recortada.
 * Recebe a imagem recortada como uma string Base64.
 * @property {() => void} onCancel - Funo de callback chamada quando o usurio cancela o recorte.
 */

/**
 * @param {ImageCropperProps} props
 * @returns {React.FC<ImageCropperProps>} Um componente React para recorte de imagens.
 *
 * @description
 * O componente `ImageCropper` oferece uma interface interativa para que os usurios possam
 * selecionar uma poro de uma imagem. Ele utiliza um `<canvas>` HTML para desenhar e
 * manipular a imagem, permitindo arrastar e visualizar a rea de recorte.
 *
 * Ele  "integrado" porque foi pensado para ser usado dentro de outro componente, como um modal.
 */
export const ImageCropper: React.FC<ImageCropperProps> = ({ imageSrc, onCrop, onCancel }) => {
    // --- CONSTANTES DE DIMENSO ---
    const {isMobile} = useWindowSize();
    /** @constant {number} CANVAS_WIDTH - Largura fixa do canvas onde a imagem ser desenhada. */
    const CANVAS_WIDTH = Math.min(isMobile ? 300 : 500, window.innerWidth * 0.8);
    /** @constant {number} CANVAS_HEIGHT - Altura fixa do canvas onde a imagem ser desenhada. */
    const CANVAS_HEIGHT = CANVAS_WIDTH * 1;
    /** @constant {number} CROP_SIZE - Tamanho (largura e altura) do quadrado de recorte. */
    const CROP_SIZE = Math.min(700, CANVAS_WIDTH * 0.8);

    // --- REFERNCIAS DOM ---
    /**
     * @type {React.RefObject<HTMLCanvasElement>} canvasRef - Ref para o elemento HTML `<canvas>`.
     * Usamos `useRef` para acessar diretamente o elemento DOM do canvas.
     */
    const canvasRef = useRef<HTMLCanvasElement>(null);
    /**
     * @type {React.MutableRefObject<HTMLImageElement | null>} imageRef - Ref para a imagem sendo carregada no canvas.
     * Guarda a instncia do objeto `Image` para que possamos desenh-la no canvas.
     */
    const imageRef = useRef<HTMLImageElement | any>(null);

    // --- ESTADOS DO COMPONENTE ---
    /**
     * @type {[boolean, React.Dispatch<React.SetStateAction<boolean>>]} isDragging - Estado que indica se o usurio est arrastando a imagem.
     */
    const [isDragging, setIsDragging] = useState(false);
    /**
     * @type {[{x: number, y: number}, React.Dispatch<React.SetStateAction<{x: number, y: number}>>]} imageOffset - Posio X e Y da imagem dentro do canvas.
     *  por meio desse estado que a imagem se move.
     */
    const [imageOffset, setImageOffset] = useState({ x: 0, y: 0 });
    /**
     * @type {[{x: number, y: number, initialOffsetX: number, initialOffsetY: number}, React.Dispatch<React.SetStateAction<{x: number, y: number, initialOffsetX: number, initialOffsetY: number}>>]} dragStart - Guarda a posio inicial do mouse e do offset da imagem quando o arrasto comea.
     * Essencial para calcular o quanto a imagem deve se mover.
     */
    const [dragStart, setDragStart] = useState({ x: 0, y: 0, initialOffsetX: 0, initialOffsetY: 0 });

    const [scale, setScale] = useState(1);

    const [pinchStartDistance, setPitchStartDistance] = useState<number | null> (null);
    const [initialScale, setInitialScale] = useState(1);

    /**
     * @function drawCanvas
     * @param {{x: number, y: number}} currentOffset - O deslocamento atual da imagem no canvas.
     * @returns {void}
     * @description
     * Esta funo  responsvel por desenhar a imagem e a rea de recorte no canvas.
     *  aqui que a mgica acontece!
     * 1. Limpa o canvas.
     * 2. Desenha a imagem na posio `currentOffset.x` e `currentOffset.y`.
     * 3. Calcula a posio do quadrado de recorte no centro do canvas.
     * 4. Desenha uma borda tracejada vermelha para indicar a rea de recorte.
     */
    const drawCanvas = (currentOffset: { x: number; y: number }, currentScale: number) => {
        const canvas = canvasRef.current;
        if (!canvas || !imageRef.current) return;

        const ctx = canvas.getContext('2d');
        if (!ctx) return;

        ctx.clearRect(0, 0, canvas.width, canvas.height);

        const scaledWidth = imageRef.current.width * currentScale;
        const scaledHeight = imageRef.current.height * currentScale;

        // AQUI  ONDE A MGICA ACONTECE!
        // `drawImage`  uma funo do contexto 2D do canvas que desenha uma imagem.
        // ctx.drawImage(imageRef.current, currentOffset.x, currentOffset.y);
        ctx.drawImage(imageRef.current, currentOffset.x, currentOffset.y, scaledWidth, scaledHeight);

        const cropX = (canvas.width - CROP_SIZE) / 2;
        const cropY = (canvas.height - CROP_SIZE) / 2;

        // Estilos para a rea de recorte
        ctx.strokeStyle = 'red'; // Cor da borda
        ctx.lineWidth = 2;       // Espessura da borda
        ctx.setLineDash([5, 5]); // Define um padro de linha tracejada (5px de linha, 5px de espao)
        ctx.strokeRect(cropX, cropY, CROP_SIZE, CROP_SIZE); // Desenha o retngulo de recorte
    };

    /**
     * @hook useEffect
     * @description
     * Este `useEffect`  executado apenas uma vez quando o componente  montado
     * ou quando `imageSrc` muda. Ele carrega a imagem, define o `imageRef` e
     * calcula a posio inicial da imagem para centraliz-la no canvas.
     * Depois de carregar, ele chama `drawCanvas` para renderizar a imagem.
     */
    useEffect(() => {
        const img = new Image(); // Cria um novo objeto Image
        img.src = imageSrc;      // Define a fonte da imagem
        img.onload = () => {
            imageRef.current = img;
            const canvas = canvasRef.current;
            if (!canvas) return;

            // 1. Calcule o fator de escala inicial para cobrir o CROP_SIZE
            const scaleX = CROP_SIZE / img.width;
            const scaleY = CROP_SIZE / img.height;
            const initialScale = Math.max(scaleX, scaleY); // Usa o maior fator para cobrir a rea de recorte

            // 2. Calcule o offset inicial com base na nova escala
            const scaledWidth = img.width * initialScale;
            const scaledHeight = img.height * initialScale;
            
            // Centraliza a imagem escalada dentro da rea de recorte
            const initialOffset = {
                x: (CANVAS_WIDTH - scaledWidth) / 2,
                y: (CANVAS_HEIGHT - scaledHeight) / 2,
            };

            // 3. Atualize os estados
            setScale(initialScale);
            setImageOffset(initialOffset);
            drawCanvas(initialOffset, initialScale);
        };
    }, [imageSrc]); // Dependncia: s roda de novo se imageSrc mudar

    /**
     * @hook useEffect
     * @description
     * Este `useEffect`  executado toda vez que `imageOffset` muda.
     * Ele redesenha a imagem no canvas com a nova posio.
     * Isso garante que a imagem se mova suavemente enquanto o usurio arrasta.
     */
    useEffect(() => {
      if (imageRef.current) {
          // Chame com o scale atual
          drawCanvas(imageOffset, scale);
      }
    }, [imageOffset, scale]); // Dependncia: roda toda vez que imageOffset muda


    const handleWheel = (e: React.WheelEvent<HTMLCanvasElement>) => {
      e.preventDefault();
      const zoomFactor = 0.1;
      const newScale = e.deltaY < 0 ? scale + zoomFactor : scale - zoomFactor;
      const newScaleCapped = Math.max(0.1, Math.min(newScale, 5));
      setScale(newScaleCapped);
    }

    /**
     * @function handleMouseDown
     * @param {React.MouseEvent<HTMLCanvasElement>} e - Evento de clique do mouse.
     * @returns {void}
     * @description
     * Chamado quando o boto do mouse  pressionado sobre o canvas.
     * Inicia o processo de arrasto, definindo `isDragging` para `true` e
     * registrando as posies iniciais do mouse e da imagem.
     */
    const handleMouseDown = (e: React.MouseEvent<HTMLCanvasElement>) => {
        setIsDragging(true);
        setDragStart({
            x: e.nativeEvent.offsetX, // Posio X do mouse relativa ao elemento
            y: e.nativeEvent.offsetY, // Posio Y do mouse relativa ao elemento
            initialOffsetX: imageOffset.x, // Posio inicial X da imagem
            initialOffsetY: imageOffset.y, // Posio inicial Y da imagem
        });
    };

    const handleTouchStart = (e: React.TouchEvent<HTMLCanvasElement>) => {
    e.preventDefault();

    // Se houver dois dedos, inicie o zoom por pina
        if (e.touches.length === 2) {
            setIsDragging(false); // Desativa o arrasto para no entrar em conflito com o zoom
            const dx = e.touches[0].clientX - e.touches[1].clientX;
            const dy = e.touches[0].clientY - e.touches[1].clientY;
            const distance = Math.sqrt(dx * dx + dy * dy);
            setPitchStartDistance(distance);
            setInitialScale(scale); // Guarda a escala atual
        } 
        // Se houver apenas um dedo, inicie o arrasto
        else if (e.touches.length === 1) {
            setIsDragging(true);
            const touch = e.touches[0];
            const rect = (e.target as HTMLCanvasElement).getBoundingClientRect();
            setDragStart({
                x: touch.clientX - rect.left,
                y: touch.clientY - rect.top,
                initialOffsetX: imageOffset.x,
                initialOffsetY: imageOffset.y,
            });
        }
    };

    const handleTouchMove = (e: React.TouchEvent<HTMLCanvasElement>) => {
        e.preventDefault();

        // Lgica para o zoom (pina)
        if (e.touches.length === 2 && pinchStartDistance !== null) {
            const dx = e.touches[0].clientX - e.touches[1].clientX;
            const dy = e.touches[0].clientY - e.touches[1].clientY;
            const newDistance = Math.sqrt(dx * dx + dy * dy);
            const zoomFactor = newDistance / pinchStartDistance;
            const newScale = initialScale * zoomFactor;
            // Limita o zoom para evitar que a imagem desaparea ou fique gigante demais
            const newScaleCapped = Math.max(0.1, Math.min(newScale, 5));
            setScale(newScaleCapped);
        } 
        // Lgica para o arrasto (pan)
        else if (isDragging && e.touches.length === 1) {
            const touch = e.touches[0];
            const rect = (e.target as HTMLCanvasElement).getBoundingClientRect();
            const scaledWidth = imageRef.current.width * scale;
            const scaledHeight = imageRef.current.height * scale;
            const dx = (touch.clientX - rect.left) - dragStart.x;
            const dy = (touch.clientY - rect.top) - dragStart.y;
            const cropX = (CANVAS_WIDTH - CROP_SIZE) / 2;
            const cropY = (CANVAS_HEIGHT - CROP_SIZE) / 2;            
            let newOffsetX = dragStart.initialOffsetX + dx;
            let newOffsetY = dragStart.initialOffsetY + dy;
            newOffsetX = Math.min(newOffsetX, cropX);
            newOffsetY = Math.min(newOffsetY, cropY);
            newOffsetX = Math.max(newOffsetX, cropX + CROP_SIZE - scaledWidth);
            newOffsetY = Math.max(newOffsetY, cropY + CROP_SIZE - scaledHeight);
            setImageOffset({ x: newOffsetX, y: newOffsetY });
        }
    };

    const handleTouchEnd = () => setIsDragging(false);
    const handleTouchCancel = () => setIsDragging(false);

    // ##### END MOBILE ###### 

    /**
     * @function handleMouseMove
     * @param {React.MouseEvent<HTMLCanvasElement>} e - Evento de movimento do mouse.
     * @returns {void}
     * @description
     * Chamado quando o mouse  movido sobre o canvas ENQUANTO o boto est pressionado.
     * Calcula o deslocamento da imagem com base no movimento do mouse e atualiza `imageOffset`.
     * Assegura que a imagem no saia da rea de recorte, limitando seus movimentos.
     */
    const handleMouseMove = (e: React.MouseEvent<HTMLCanvasElement>) => {
        if (!isDragging || !imageRef.current) return; // S move se estiver arrastando e a imagem existir

        const dx = e.nativeEvent.offsetX - dragStart.x; // Variao em X
        const dy = e.nativeEvent.offsetY - dragStart.y; // Variao em Y
        const scaledWidth = imageRef.current.width * scale;
        const scaledHeight = imageRef.current.height * scale;
        const cropX = (CANVAS_WIDTH - CROP_SIZE) / 2;
        const cropY = (CANVAS_HEIGHT - CROP_SIZE) / 2;
        let newOffsetX = dragStart.initialOffsetX + dx;
        let newOffsetY = dragStart.initialOffsetY + dy;
        
        newOffsetX = Math.min(newOffsetX, cropX); // No permite mover a imagem muito para a direita
        newOffsetY = Math.min(newOffsetY, cropY); // No permite mover a imagem muito para baixo
        newOffsetX = Math.max(newOffsetX, cropX + CROP_SIZE - scaledWidth);
        newOffsetY = Math.max(newOffsetY, cropY + CROP_SIZE - scaledHeight);

        setImageOffset({ x: newOffsetX, y: newOffsetY }); // Atualiza a posio da imagem
    };

    /**
     * @function handleMouseUp
     * @returns {void}
     * @description
     * Chamado quando o boto do mouse  liberado.
     * Finaliza o processo de arrasto, definindo `isDragging` para `false`.
     */
    const handleMouseUp = () => setIsDragging(false);

    /**
     * @function handleCrop
     * @returns {void}
     * @description
     * Chamado quando o boto "Recortar e Salvar"  clicado.
     * 1. Cria um novo canvas temporrio do tamanho exato da rea de recorte.
     * 2. Desenha APENAS a poro da imagem que est dentro da rea de recorte nesse novo canvas.
     * 3. Converte o contedo do novo canvas para uma string Base64 no formato WebP.
     * 4. Chama a funo `onCrop` passada via props com a imagem recortada.
     */
    const handleCrop = () => {
        if (!imageRef.current) return;

        const resultCanvas = document.createElement('canvas');
        resultCanvas.width = CROP_SIZE;
        resultCanvas.height = CROP_SIZE;
        const resultCtx = resultCanvas.getContext('2d');
        if (!resultCtx) return;

        // A lgica para o cropXOnCanvas e cropYOnCanvas continua a mesma
        const cropXOnCanvas = (CANVAS_WIDTH - CROP_SIZE) / 2;
        const cropYOnCanvas = (CANVAS_HEIGHT - CROP_SIZE) / 2;
        
        // As coordenadas da fonte e o tamanho agora precisam levar em conta o scale
        const sourceX = (cropXOnCanvas - imageOffset.x) / scale;
        const sourceY = (cropYOnCanvas - imageOffset.y) / scale;
        const sourceWidth = CROP_SIZE / scale;
        const sourceHeight = CROP_SIZE / scale;

        resultCtx.drawImage(
            imageRef.current,
            sourceX, sourceY, // Posio de incio da cpia na imagem original (dividida pelo scale)
            sourceWidth, sourceHeight, // Largura e altura da poro a ser copiada
            0, 0,
            CROP_SIZE, CROP_SIZE
        );

        const webpData = resultCanvas.toDataURL('image/webp', IMAGE_WEBP_QUALITY);
        onCrop(webpData);
    };

    return (
        <div className="bg-white rounded shadow-xl p-4 sm:p-6 text-center">
            <h3 className="text-xl font-bold mb-4">Posicione sua Foto</h3>
            <div className="border-2 border-dashed border-gray-300 rounded-lg inline-block">
                <canvas
                    ref={canvasRef}
                    width={CANVAS_WIDTH}
                    height={CANVAS_HEIGHT}
                    onMouseDown={handleMouseDown}
                    onMouseMove={handleMouseMove}
                    onMouseUp={handleMouseUp}
                    onMouseLeave={handleMouseUp}  
                    
                    onTouchStart={handleTouchStart}
                    onTouchMove={handleTouchMove}
                    onTouchEnd={handleTouchEnd}
                    onTouchCancel={handleTouchCancel}

                    onWheel={handleWheel}

                    className="rounded cursor-move"
                />
            </div>
            <div className="mt-6 d-flex justify-content-center gap-4">
                <button onClick={onCancel} className="btn btn-danger px-6 py-2 rounded-md text-gray-700 bg-gray-200 hover:bg-gray-300 font-semibold">
                    <i className="fa fa-x text-white"></i> Cancelar
                </button>
                <button onClick={handleCrop} className="btn button-photo btn-primary px-6 py-2 rounded-md text-white hover:opacity-90 font-semibold inline-flex items-center">
                    <i className="text-white fa fa-save"></i> Salvar
                </button>
            </div>
        </div>
    );
};